#carouselContainer {
width: 100vw;
margin: 0 auto;
}

.carouselImgs {
position: relative;
width: 100vw;
height: auto;
border-radius: 15px;
}

.carouselRadio {
  position: relative;
  top: -95px;
  z-index: 1;
}

#carouselBtns {
  position: relative;
  width: 100vw;
  padding: 25px;
  top: -125px;
z-index: 1;
display: flex;
justify-content: center;
align-items: center;
}

.btnBoxes {
flex-shrink: 0;
}



#forwardBtn {
z-index: 1;
margin: 15px;
max-width: 40px;
opacity: 50%;
}

#forwardBtn:hover {
    transform: scale(1.2);
    opacity: 100%;
}

.homeChildren {
  width: 100vw;
text-align: center;
animation: slide 1s ease 0s 1 normal forwards;
padding: 15px;
margin: 0 auto;
border-radius: 12px;
}

#homeContainer {
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}


#backBtn {
    z-index: 1;
    position: relative;
    margin: 15px;
    max-width: 40px;
    opacity: 50%;
    }

    #backBtn:hover {
        transform: scale(1.2);
        opacity: 100%;
    }

    #viewServBtn {
      text-align: center;
      margin: 0 auto;
      width: 175px;
      height: 60px;
      font-size: 20px;
      background-color: #2f2f2f;
      border-radius: 15px;
      color: white;
    }

    #viewServBtn:hover {
    background-color: #FA8072;
  user-select: none;
    }

@keyframes slide {
    0% {
      opacity: 0;
      transform: translateX(-250px);
    }
  
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }

