#servicesContainer {
display: flex;
flex-wrap: wrap;
justify-content: center;
flex-direction: column;
align-content: center;
margin: 15px;
padding: 5px;
font-family: 'Playfair Display', serif;
}

.servicesChildren {
  border-radius: 12px;
  background: #ffffff;
  box-shadow:  -20px 20px 60px #d9d9d9,
               20px -20px 60px #ffffff;
margin: 20px;
padding: 10px;
width: 100vw;
animation: Slide 1s ease 0s 1 normal backwards
}

.servicePhotos {
  position: relative;
width: 350px;
border-radius: 10px;
height: auto;
}


@keyframes Slide {
    0% {
      opacity: 0;
      transform: translateX(-250px);
    }
  
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }

  