#aboutContainer {
display: flex;
flex-direction: column;
justify-content: center;
align-content: center;
align-items: center;
height: 100vh;
border-radius: 50px;
background: #FFFFFF;
box-shadow:  -20px 20px 60px #d9d9d9,
             20px -20px 60px #ffffff;
}

.aboutChildren {
max-width: 100vw;
height: auto;
animation: Scale 1s ease 0s 1 normal backwards;
}

.aboutPhotos {
width: 350px;
border-radius: 10px;
height: auto;
}

@keyframes Scale {
    0% {
      transform: scale(0);
    }
  
    100% {
      transform: scale(1);
    }
  }