#contactContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    height: 100%;
    border-radius: 50px;
    color: #2f2f2f;
    background: #ffffff;
    box-shadow:  -20px 20px 60px #d9d9d9,
                 20px -20px 60px #ffffff;
}



.contactChildren {
    margin: 2px;
    padding: 5px;
  
}

.contactForm {
font-size: 15px;
width: 350px;
height: 50px;
border-radius: 12px;
}

.contactForm:focus {
background-color: #FA8072;
}

#messageArea {
height: 100px;
width: 350px;
}

#submitBtn {
width: 100px;
height: 50px;
padding: 5px;
margin: 10px;
font-size: 16px;
background-color: #2f2f2f;
border-radius: 15px;
color: white;
}

#submitBtn:hover {
    background-color: #FA8072;
    transform: scale(1.1);
}

